/**
 * 在宿主环境中加载主题配置
 */
import type { Dispatch } from 'redux';
import defaultTheme from './defaultTheme';
import { loadTheme, LoadThemeArgs } from './loadTheme';
import setCssVars from './setCssVars';

const THEME_SET = '@@THEME/SET';

interface Args extends LoadThemeArgs {
  theme?: object;
  dispatch: Dispatch;
}

function setTheme(theme: object, dispatch: Dispatch<any>) {
  setCssVars(theme);

  dispatch({
    type: THEME_SET,
    payload: theme,
  });
}

export default async ({
  theme,
  djcGatewayBaseUrl,
  projectKey,
  themeKey,
  dispatch,
  cache,
  ttl,
}: Args) => {
  const configTheme = await loadTheme({ djcGatewayBaseUrl, projectKey, themeKey, cache, ttl });

  const finalTheme = { ...defaultTheme, ...configTheme, ...theme };

  setTheme(finalTheme, dispatch);

  return (updateTheme: object) => {
    setTheme({ ...finalTheme, ...updateTheme }, dispatch);
  };
};
