import request from '@/utils/request';
import { useEffect, useState } from 'react';
import useTheme from './useTheme';

export interface LoadThemeArgs {
  djcGatewayBaseUrl: string;
  projectKey?: string;
  themeKey?: string;
  cache?: boolean;
  ttl?: number;
}

export const loadTheme = async ({
  djcGatewayBaseUrl,
  projectKey,
  themeKey = 'default',
  cache = true,
  ttl = 60 * 30,
}: LoadThemeArgs) => {
  const portalKey = projectKey || `${window.top?.location.host}${window.top?.location.pathname}`;

  try {
    const { data } = await request(`${djcGatewayBaseUrl}/djcsupport/v1/portal-infos/portal-theme`, {
      params: { portalKey, themeKey },
      cache,
      ttl,
    });

    return data && data.themeSettings ? JSON.parse(data.themeSettings) : {};
  } catch {
    return {};
  }
};

export const useLoadTheme = ({
  djcGatewayBaseUrl,
  projectKey,
  themeKey,
  cache,
  ttl,
}: LoadThemeArgs) => {
  const [theme, setTheme] = useState();
  const configTheme = useTheme();

  /**
   * 宿主环境从服务器端加载配置
   */
  useEffect(() => {
    // eslint-disable-next-line no-restricted-globals
    if (top === self) {
      loadTheme({ djcGatewayBaseUrl, projectKey, themeKey, cache, ttl }).then(setTheme);
    }
  }, [cache, djcGatewayBaseUrl, projectKey, themeKey, ttl]);

  /**
   * 子应用从 redux 中获取配置
   */
  // eslint-disable-next-line no-restricted-globals
  if (top !== self) {
    return configTheme;
  }
  return theme;
};
