import isMSIE from '@/utils/isMSIE';
import cssVars from 'css-vars-ponyfill';

/**
 * 保存上一次的变量, 用于找出需要清理的变量
 */
let prevVars;

/**
 * 找出在新的 vars 中不存在的 prevVars key
 * 这部分变量需要清除, 避免干扰
 */
const findNeedRemoveKeys = (vars: object, pVars?: object) => {
  if (!pVars) return [];

  const removeKeys: Array<string> = [];

  const pKeys = Object.keys(pVars);
  const Keys = Object.keys(vars);

  pKeys.forEach(key => {
    if (!Keys.includes(key)) {
      removeKeys.push(key);
    }
  });

  return removeKeys;
};

/**
 * 需要删除的key, 值设置为 null
 */
const createRemoveVars = (removeKeys: Array<string>) => {
  const o = {};
  removeKeys.forEach(key => {
    o[key] = null;
  });
  return o;
};

export default (vars: object) => {
  const removeKeys = findNeedRemoveKeys(vars, prevVars);
  const removeVars = createRemoveVars(removeKeys);

  cssVars({
    variables: { ...vars, ...removeVars },
    watch: isMSIE(),
  });

  prevVars = vars;
};
