/**
 * 在宿主环境中加载主题配置
 * 或
 * 在子应用中加载宿主环境的
 * *必须放置在 redux <Provider></Provider> 内
 */
import React, { useEffect, useMemo, useState } from 'react';
import { useDispatch } from 'react-redux';
import IEDisableCSSOMInjection from '@/utils/IEDisableCSSOMInjection';
import defaultTheme from './defaultTheme';
import { LoadThemeArgs, useLoadTheme } from './loadTheme';
import setCssVars from './setCssVars';

const THEME_SET = '@@THEME/SET';

interface Props extends LoadThemeArgs {
  theme?: object;
  children: React.ReactNode;
  renderAfterLoaded?: boolean;
}

export default ({
  theme,
  children,
  djcGatewayBaseUrl,
  projectKey,
  themeKey,
  renderAfterLoaded = true,
  cache,
  ttl,
}: Props) => {
  const configTheme = useLoadTheme({ djcGatewayBaseUrl, projectKey, themeKey, cache, ttl });
  const dispatch = useDispatch();
  const [loaded, setLoaded] = useState(false);

  const finalTheme = useMemo(
    () => ({ ...defaultTheme, ...configTheme, ...theme }),
    [configTheme, theme]
  );

  /**
   * 宿主的主题设置存放到 redux 中
   */
  useEffect(() => {
    // eslint-disable-next-line no-restricted-globals
    if (top === self) {
      dispatch({
        type: THEME_SET,
        payload: finalTheme,
      });
    }
  }, [dispatch, finalTheme]);

  useEffect(() => {
    /**
     * 获取数据成功后设置, 避免页面闪烁
     */
    if (configTheme) {
      setCssVars(finalTheme);
      setLoaded(true);
    }
  }, [configTheme, finalTheme]);

  if (renderAfterLoaded && !loaded) return null;

  return <IEDisableCSSOMInjection>{children}</IEDisableCSSOMInjection>;
};
